import { postJSON } from './http';

function saveGoods(combo, goodIds) {
    return postJSON(null == combo.id ? '/dc/combo/add' : '/dc/combo/mod', {
        ...combo, goodIds
    });
}
function queryComboById(id) {
    return postJSON('/dc/combo/find', {id});
}
function queryComboGoods(comboType) {
    return postJSON('/dc/goods/list', { comboType, pageSize : 500});
}

function queryPageByName(comboName, pageNum, pageSize, status = null) {
    const params = status ? {comboName, pageNum, pageSize, status} : {comboName, pageNum, pageSize};
    return postJSON('/dc/combo/query', params)
}

function updateComboStatus(comboId, status) {
    return postJSON('/dc/combo/upStatus', {comboId, status});
}

function queryComboStar() {
    return postJSON('/dc/order/getOrderPersonCnt');
}

function queryComboTopTen(type) {
    return postJSON('/dc/order/getOrderScore', { type, num : 10});
}

export {
    saveGoods,
    queryComboById,
    queryComboGoods,
    queryPageByName,
    updateComboStatus,
    queryComboStar,
    queryComboTopTen
}