<template>
    <a-form-model 
        layout="horizontal" 
        :model="item" 
        :rules="rules"
        :labelCol="{span: 6}"
        :wrapperCol="{span: 12}"
        ref="form">
        <a-spin :spinning="querying">
            <a-alert v-if="hasError" type="error" :message="errorMsg"  class="ke-fs-mg-b" />
            <a-form-model-item label="菜品图片" style="margin-bottom:0px;">
                <a-input type="hidden" v-model="item.goodsImg" />
                <a-upload name="file" 
                    accept="image/*" 
                    action="/common/upload?uid=1" 
                    listType="picture-card" 
                    :show-upload-list="false" 
                    @change="_onGoodsImgUpload">
                    <img v-if="item.goodsImg && item.goodsImg.length > 0" :src="`/common/download?fileName=${item.goodsImg}&uid=1`" alt="菜品封面" />
                    <div v-else>
                        <a-icon :type="uploading ? 'loading' : 'plus'" />
                        <div class="ant-upload-text">上传</div>
                    </div>
                </a-upload>
            </a-form-model-item>
            <a-form-model-item label="菜品名称" prop="goodsName" required>
                <a-input v-model="item.goodsName" placeholder="请输入菜品名称" />
            </a-form-model-item>
            <a-form-model-item label="菜品分类" required>
                <a-select v-model="item.goodsType" placeholder="请选择菜品分类">
                    <a-select-opt-group label="简餐">
                        <a-select-option value="1">主菜</a-select-option>
                        <a-select-option value="2">辅菜</a-select-option>
                        <a-select-option value="3">小菜</a-select-option>
                        <a-select-option value="4">例汤</a-select-option>
                    </a-select-opt-group>
                    <a-select-opt-group label="面食">
                        <a-select-option value="5">面条</a-select-option>
                    </a-select-opt-group>
                </a-select>
            </a-form-model-item>
            <a-form-model-item label="菜品说明">
                <a-textarea v-model="item.goodsDesc" placeholder="请输入菜品描述" :rows="3" allowClear />
            </a-form-model-item>
            <a-form-model-item :wrapper-col="{ span: 12, offset: 6 }">
                <a-button type="primary" :loading="loading" @click="_submitForm">保 存</a-button>
            </a-form-model-item>
        </a-spin>
    </a-form-model>
</template>

<script>
    import { saveGoods, queryGoodsById } from '@/api/goods';
    export default {
        props : {
            foodsId : {
                type    : Number,
                default : null
            }
        },
        data() {
            return {
                item : {
                    id        : null,
                    goodsName : '',
                    goodsType : '1',
                    goodsDesc : '',
                    goodsImg  : '',
                    status    : '1'
                },
                rules : {
                    goodsName : [
                        { required : true, message : '菜品名称不能为空!', trigger : 'blur' } 
                    ],
                },
                uploading : false,
                loading   : false,
                hasError  : false,
                querying  : false,
                errorMsg  : null
            }
        },
        methods : {
            _submitForm() {
                this.$refs.form.validate(r => {
                    r && this._saveGoods();
                    return r;
                })
            },
            _onGoodsImgUpload(e) {
                const status = e.file.status;
                if('uploading' === status) {
                    this.uploading = true;
                } else if('done' === status) {
                    this.item.goodsImg = e.file.response.fileName;
                    this.uploading = false;
                    console.log(this.item.goodsImg);
                } else if('error' === status) {
                    this.$message.error('上传图片失败!');
                }
            },
            _saveGoods() {
                this.loading = true;
                this.hasError = false;
                saveGoods(this.item)
                    .then(() => {
                        this.loading = false;
                        this.$emit('success');
                    })
                    .catch(err => {
                        this.loading = false;
                        this.hasError = true;
                        this.errorMsg = `保存失败,原因:${err}`;
                    });
            },
            
            _queryFoodsById(id) {
                this.querying = true;
                queryGoodsById(id)
                    .then(r => {
                        this.item = r;
                        this.querying = false;
                    })
                    .catch(err => this.$emit('fail', err));
            }
        },

        created() {
            this.foodsId && this._queryFoodsById(this.foodsId);
        }
    }
</script>
