<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16" class="ke-fs-mg-b">
                <a-col :span="8">
                    <a-range-picker :defaultValue="defaultValue" @change="_search" />
                </a-col>
            </a-row>
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="comboCode"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="pagination"
                        @change="_onPageChange">
                        <span slot="type" slot-scope="type">
                           {{'1' == type ? '简餐' : '面食'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`/common/download?fileName=${img}`" />
                        </span>
                    </a-table>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
    import moment from 'moment';
    import { getHistoryOrders } from '@/api/order';
    export default {
        data() {
            return {
                columns    : [{
                    title       : '套餐名称',
                    dataIndex   : 'comboName'
                },{
                    title       : '套餐类型A',
                    dataIndex   : 'comboType',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '套餐图片',
                    dataIndex   : 'comboImg',
                    scopedSlots : { customRender : 'img' }
                },{
                    title       : '下单数',
                    dataIndex   : 'count'
                }],
                defaultValue : [],
                comboList    : [],
                pagination   : {
                    pageSize : 10
                },
                loading      : false,
                pageNo       : 1
            }
        },
        methods : {
            _search(dates) {
                this._queryOrders(dates[0].format('YYYY-MM-DD'), dates[1].format('YYYY-MM-DD'));
            },
            _queryOrders(start, end) {
                this.loading = true ;
                getHistoryOrders(start, end)
                    .then(r => {
                        this.loading = false;
                        this.comboList = (r['rows'] || []).sort((a,b) => a.count - b.count);
                        const pObj = { ...this.paginatio};
                        pObj.total = r['total'] || 0;
                        this.pagination = pObj;
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询今日订单详情失败,原因:${err}`)
                    })
            },
            _onPageChange({ current }){
                this.pageNo = current;
                this._queryOrders();
            }
        },
        created() {
            const now = Date.now(),
                  old = new Date(now - 1000 * 60 * 60 * 24 * 7),
                end = moment(now)/*.format('YYYY-MM-DD')*/,
                start   = moment(old)/*.format('YYYY-MM-DD')*/; 
            this.defaultValue = [ start, end ];
            this._queryOrders(start.format('YYYY-MM-DD'), end.format('YYYY-MM-DD'));
        }
    }
</script>
