<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="false">
                        <span slot="type" slot-scope="type">
                           {{'1' == type ? '简餐' : '面食'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`/common/download?fileName=${img}`" />
                        </span>
                        <span slot="index" slot-scope="index" class="combo-index" :class="{'good' : isGoodTop10}">{{index}}</span>
                    </a-table>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
    import { queryComboTopTen } from '@/api/combo';
    export default {
        data() {
            return {
                columns    : [{
                    title       : '排名',
                    dataIndex   : 'index',
                    scopedSlots : { customRender: 'index' }
                },{
                    title       : '套餐名称',
                    dataIndex   : 'combo_name'
                },{
                    title       : '套餐编码',
                    dataIndex   : 'combo_code'
                },{
                    title       : '套餐类型A',
                    dataIndex   : 'combo_type',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '套餐图片',
                    dataIndex   : 'combo_img',
                    scopedSlots : { customRender : 'img' }
                }],
                comboList  : [],
                loading    : false
            }
        },

        watch : {
            '$route' : function() {
                this._queryComboTopTen(this.$router.currentRoute.query.type);
            }
        },
        methods : {
            _queryComboTopTen(type) {
                this.isGoodTop10 = '1' == type;
                this.loading = true;
                queryComboTopTen(type)
                    .then(r => {
                        this.loading = false;
                        const list = r || [];
                        this.comboList = list.map((v,i) => {
                            v['index'] = i+1;
                            return v;
                        });
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询Top10失败,原因:${err}`)
                    });
            }
        },

        created() {
            this._queryComboTopTen(this.$router.currentRoute.query.type);
        }
    }
</script>