import axios from 'axios';
import QS from 'qs';

const http = axios.create({
    baseURL      : '/', 
    timeout      : 20000,
    headers      : {
        'Content-Type' : 'application/x-www-form-urlencoded;charset=UTF-8'
    },
    responseType : 'json'
});


http.interceptors.request.use(config => {
    const uid = window.localStorage ? window.localStorage.getItem('uid') : window.___fsAppUID___;
    config.headers['uid'] = uid;
    return config;
});

http.interceptors.response.use(
    res => {
        if(200 === res.status) {
            if(0 === res.data.code) {
                return Promise.resolve(res.data.data || {});
            } else {
                return Promise.reject(res.data.msg || '未知错误!');
            }
        } else {
            return Promise.reject(res);
        }
    },
    err => {
        let errorMsg ;
        if(err && err.response) {
            switch(err.response.status) {
                case 400:
                    errorMsg = '错误的请求!';
                    break;
                case 401:
                    errorMsg = '未认证,请重新认证!';
                    break;
                case 403:
                    errorMsg = '拒绝访问!';
                    break;
                case 404:
                    errorMsg = '请求错误,未找到该资源!';
                    break;
                case 405:
                    errorMsg = '请求方法未允许!';
                    break;
                case 408 :
                    errorMsg = '请求超时!';
                    break;
                case 500 :
                    errorMsg = '服务器内部错误!';
                    break;
                default:
                    errorMsg = `连接错误${err.response.status}`;
            }
        } else {    
            errorMsg = err || '未知错误!';
        }
        return Promise.reject(errorMsg);
    }
)


function postJSON(url, params = {}) {
    return http.post(url, QS.stringify(params));
}

export {
    postJSON
}