<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="false">
                        <span slot="type" slot-scope="type">
                           {{'1' == type ? '简餐' : '面食'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`/common/download?fileName=${img}`" />
                        </span>
                        <span slot="avg" slot-scope="avg" class="combo-star" :class="{'hight' : avg > 3}">
                            {{Math.floor(avg  * 10) / 10 }}
                        </span>
                    </a-table>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
    import { queryComboStar } from '@/api/combo';
    export default {
        data() {
            return {
                columns    : [{
                    title       : '套餐名称',
                    dataIndex   : 'combo_name'
                },{
                    title       : '套餐编码',
                    dataIndex   : 'combo_code'
                },{
                    title       : '套餐类型A',
                    dataIndex   : 'combo_type',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '套餐图片',
                    dataIndex   : 'combo_img',
                    scopedSlots : { customRender : 'img' }
                },{
                    title       : '平均评分',
                    dataIndex   : 'avg',
                    scopedSlots : { customRender : 'avg' }
                },{
                    title       : '评分人数',
                    dataIndex   : 'cnt'
                }],
                comboList  : [],
                loading    : false
            }
        },
        methods : {
            _queryComboStar() {
                this.loading = true ;
                queryComboStar()
                    .then(r => {
                        this.loading = false;
                        this.comboList = r || [];
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询星级评分失败,原因:${err}`)
                    })
            }
        },
        created() {
            this._queryComboStar();
        }
    }
</script>
