<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="comboList" 
                        :loading="loading" 
                        :pagination="false">
                        <span slot="type" slot-scope="type">
                           {{'1' == type ? '简餐' : '面食'}}
                        </span>
                        <span class="combo-cover" slot="img" slot-scope="img">
                            <img :src="`/common/download?fileName=${img}`" />
                        </span>
                    </a-table>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
    import { getTodayOrders } from '@/api/order';
    export default {
        data() {
            return {
                columns    : [{
                    title       : '套餐名称',
                    dataIndex   : 'comboName'
                },{
                    title       : '套餐类型A',
                    dataIndex   : 'comboType',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '套餐图片',
                    dataIndex   : 'comboImg',
                    scopedSlots : { customRender : 'img' }
                },{
                    title       : '下单数',
                    dataIndex   : 'count'
                }],
                comboList  : [],
                loading    : false
            }
        },
        methods : {
            _queryOrders() {
                this.loading = true ;
                getTodayOrders()
                    .then(r => {
                        this.loading = false;
                        this.comboList = (r || []).sort((a,b) => a.count - b.count);
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询今日订单详情失败,原因:${err}`)
                    })
            }
        },
        created() {
            this._queryOrders();
        }
    }
</script>
