import Vue from 'vue'
import VueRouter from 'vue-router'
import Portal from '@/views/portal/Portal';
import FoodsList from '@/views/foods/FoodsList'
import ComboList from '@/views/combo/ComboList';
import ComboPublish from '@/views/combo/ComboPublish';
import CurrentCombo from '@/views/combo/CurrentCombo';
import ComboStar from '@/views/combo/ComboStar';
import ComboPerson from '@/views/combo/ComboPerson';
import ComboTopTen from '@/views/combo/ComboTopTen';
import TodayOrders from '@/views/order/TodayOrders';
import HistoryOrders from '@/views/order/HistoryOrders';

Vue.use(VueRouter)

const routes = [{
    path      : '/',
    component : Portal,
    children  : [{
        path      : '/foodsMgr',
        component : FoodsList,
        meta      : {
            name : '菜品管理'
        }
    },{
        path      : '/comboMgr',
        component : ComboList,
        meta      : {
            name : '套餐管理'
        }
    },{
        path      : '/comboPublish',
        component : ComboPublish,
        meta      : {
            name : '套餐库'
        }
    },{
        path      : '/currentCombo',
        component : CurrentCombo,
        meta      : {
            name  : '当前套餐'
        }
    },{
        path      : '/comboStar',
        component : ComboStar,
        meta      : {
            name : '星级评分'
        }
    },{
        path      : '/comboPerson',
        component : ComboPerson,
        meta      : {
            name : '点评人员'
        }
    },{
        path      : '/comboTop',
        component : ComboTopTen,
        meta      : {
            name : '菜品Top10'
        }
    },{
        path      : '/comboTop?type=2',
        component : ComboTopTen,
        meta      : {
            name : '菜品Top10'
        }
    }, {
        path      : '/todayOrders',
        component : TodayOrders,
        meta      : {
            name : '当日订单统计'
        }

    }, {
        path      : '/historyOrders',
        component : HistoryOrders,
        meta      : {
            name : '历史订单统计'
        }

    },{
        path      : '',
        redirect  : '/todayOrders'
    }]
}];

const router = new VueRouter({
  routes
});

export default router;
