<template>
    <div class="ke-fs-admin-portal-main__content__inner">
        <div class="ke-fs-box">
            <a-row :gutter="16" class="ke-fs-mg-b">
                <a-col :span="8">
                    <a-input-search  placeholder="请输入菜品名称..." @search="_search" />
                </a-col>
                <a-col :span="8" :offset="8" class="ke-fs-ta-r">
                    <a-button type="primary" @click="_newFoods">
                        <a-icon type="plus" />
                        <span>新增菜品</span>
                    </a-button>
                </a-col>
            </a-row>
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-table 
                        rowKey="id"
                        :columns="columns" 
                        :dataSource="goods" 
                        :loading="loading" 
                        :pagination="pagination" 
                        @change="_onPageChange">
                        <span slot="type" slot-scope="type">{{types[type]}}</span>
                        <template slot="status" slot-scope="status">
                            <a-tag v-if="1 == status" color="green">启用</a-tag>
                            <a-tag v-else color="red">禁用</a-tag>
                        </template>
                        <span class="foods-cover" slot="img" slot-scope="img">
                            <img :src="`/common/download?fileName=${img}`" />
                        </span>
                        <div class="foods-action" slot="actions" slot-scope="id">
                            <a-button size="small" icon="edit" @click="_modifyFoodsById(id)" />
                            <a-popconfirm title="是否确认删除当前菜品?" @confirm="_removeFoodsById(id)">
                                <a-button type="danger" size="small" icon="delete" />
                            </a-popconfirm>
                        </div>
                    </a-table>
                </a-col>
            </a-row>
        </div>
        <a-modal title="编辑" :width="680" :visible="isShowEditor" :footer="null" @cancel="isShowEditor=false" destroyOnClose>
            <FoodsEditor :foodsId="edtFoodsId" @success="_onSaveFoodsSuccess" />
        </a-modal>
    </div>
</template>

<script>
    import { queryPageByName, removeGoodsById } from '@/api/goods';
    import FoodsEditor from '../../components/foods-editor/FoodsEditor';
    const FOODS_TYPES = {
        '1' : '主菜',
        '2' : '辅菜',
        '3' : '小菜',
        '4' : '例汤',
        '5' : '面条'
    }
    export default {
        components : {
            FoodsEditor
        },
        data() {
            return {
                columns : [{
                    title       : '菜品名称',
                    dataIndex   : 'goodsName'
                }, {
                    title       : '菜品分类',
                    dataIndex   : 'goodsType',
                    scopedSlots : { customRender: 'type' }
                },{
                    title       : '菜品图片',
                    dataIndex   : 'goodsImg',
                    scopedSlots : { customRender: 'img' }
                },{
                    title       : '菜品状态',
                    dataIndex   : 'status',
                    scopedSlots : { customRender: 'status' }
                },{
                    title       : '菜品描述',
                    dataIndex   : 'goodsDesc'
                },{
                    title       : '操作',
                    dataIndex   : 'id',
                    scopedSlots : { customRender: 'actions' }
                }],
                pagination   : {
                    pageSize : 10
                },
                types        : FOODS_TYPES,
                goods        : [],
                loading      : false,
                isShowEditor : false,
                edtFoodsId   : null,
                pageNo       : 1
            }
        },
        methods : {
            _search(value) {
                this.pageNo = 1;
                this._queryFoodsList(value);
            },
            _onPageChange(e) {
                this.pageNo = e.current;
                this._queryFoodsList();
            },

            _newFoods() {
                this.edtFoodsId = null;
                this.isShowEditor = true;
            },
            _modifyFoodsById(id) {
                this.edtFoodsId = id;
                this.isShowEditor = true;
            },
            _removeFoodsById(id) {
                removeGoodsById(id)
                    .then(() => {
                        this.$message.success('删除成功!');
                        this._queryFoodsList();
                    })
                    .catch(err => this.$message.erro(`删除失败,原因:${err}`))
            },
            _onSaveFoodsSuccess() {
                this.isShowEditor = false;
                this.$message.success('保存成功!');
                this._queryFoodsList();
            },

            _queryFoodsList(name = null) {
                this.loading = true;
                queryPageByName(name, this.pageNo, this.pagination.pageSize)
                    .then(r => {
                        this.loading = false;
                        this.goods =  r['rows'] || [];
                        const pObj = { ...this.paginatio};
                        pObj.total = r['total'] || 0;
                        this.pagination = pObj;
                    })
                    .catch(err => {
                        this.loading = false;
                        this.$message.error(`查询菜品失败,原因:${err}`);
                    });
            },
            
        },

        created() {
            this._queryFoodsList(null);
        }


    }
</script>