import { postJSON } from './http';

function saveGoods(goods) {
    return postJSON(null == goods.id ? '/dc/goods/add' : '/dc/goods/mod', goods);
}

function removeGoodsById(id) {
    return postJSON('/dc/goods/del', { id });
}

function queryGoodsById(id) {
    return postJSON('/dc/goods/find', { id });
}

function queryPageByName(goodsName, pageNum, pageSize) {
    return postJSON('/dc/goods/list', { goodsName, pageNum, pageSize });
}

export {
    saveGoods,
    queryGoodsById,
    queryPageByName,
    removeGoodsById
}